@import url(https://fonts.googleapis.com/css?family=Raleway:400,,800,900);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  margin-top: 10%;
  margin-bottom: 1%;
  height: 20vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 6s linear;
            animation: App-logo-spin infinite 6s linear;
  }
}
form{
  display: block;
  justify-content: center;
  height: 170px;
  width: 220px;
}
.formInput{
  display: flex;
  justify-content: center;
  background-color: white;
  border: 2px solid rgb(34, 62, 112);
  border-radius: 5px;
}

.sumbitArea{
  margin-top: 1%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
.updateButton{
  background: linear-gradient(145deg, rgb(29, 39, 94) 30%, rgb(110, 7, 114) 90%);
  box-shadow: 0 3px 5px 2px rgba(0, 0, 0, 0.3);
  font-family: "Raleway";

}
.home {
  width: 100%;
  height: 30%;
  color: white;
}
.stat{
  width: 30%;
  height: 90%;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  padding: 1.5rem;
}

.App-link {
  color: #61dafb;
}

.textDrawer{
  font-size: 20px;
  font-family: "Raleway";
  color: rgb(241, 228, 228);
  text-align: center;
}

.about{
  margin-top: 1%;
  padding-top: 1%;
  padding-left: 5%;
  padding-right: 5%;
  margin-right: 5%;
  margin-left: 5%;
}

@-webkit-keyframes App-logo-spin {
  0% { -webkit-transform: scale(1); transform: scale(1); }
  50% {-webkit-transform: scale(1.3);transform: scale(1.3); }
  50% { -webkit-transform: scale(1.3); transform: scale(1.3); }
  100% { -webkit-transform: scale(1); transform: scale(1); }
}

@keyframes App-logo-spin {
  0% { -webkit-transform: scale(1); transform: scale(1); }
  50% {-webkit-transform: scale(1.3);transform: scale(1.3); }
  50% { -webkit-transform: scale(1.3); transform: scale(1.3); }
  100% { -webkit-transform: scale(1); transform: scale(1); }
}
.news-img{
    width: 400px;
    height: 270px;
}
.card-title{
    height: 210px; 
}
.card-content{
    height: 120px;
}
.grid-card{
    display: flex;
    justify-content: center;
    align-items: center;
}
